import {AuthenticationService} from './../services/authentication.service';
import {Component, OnInit, Renderer} from '@angular/core';
import {FormBuilder, FormGroup, Validators, AbstractControl, ReactiveFormsModule} from '@angular/forms';
import {ActivatedRoute, Router} from '@angular/router';
import {MenuController} from '@ionic/angular';
// import * as hammerjs from 'hammerjs';
import { SafariViewController } from '@ionic-native/safari-view-controller/ngx';
import { AppVersion } from '@ionic-native/app-version/ngx';

@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss']
})
export class LoginPage implements OnInit {
  returnUrl: string;
  appVersionString: string;

  loginFormGroup: FormGroup;

  username:AbstractControl;
  password:AbstractControl;

  constructor(
    private authService: AuthenticationService,
    private route: ActivatedRoute,
    private router: Router,
    private menuCtrl: MenuController,
    private formBuilder: FormBuilder,
    private safariViewController: SafariViewController,
    private appVersion: AppVersion
  ) {

    this.loginFormGroup = formBuilder.group({
      username:['', Validators.required],
      password:['', Validators.required]
    });

    this.username = this.loginFormGroup.controls['username'];
    this.password = this.loginFormGroup.controls['password'];
  }

  ngOnInit() {
    // reset login status
    // this.authenticationService.logout();

    // get return url from route parameters or default to '/'
    this.returnUrl = this.route.snapshot.queryParams['returnUrl'] || '/';

    this.appVersion.getVersionNumber().then(value => {
      this.appVersionString = value;
    }).catch(err => {
      console.error(`Automatic version # fetch failed: ${err}`);
    });
  }

  signup() {
    this.safariViewController.isAvailable()
      .then((available: boolean) => {
          if (available) {

            this.safariViewController.show({
              url: 'https://ciphertrace.com/ios-signup/',
              hidden: false,
              animated: true,
              transition: 'curl',
              enterReaderModeIfAvailable: true,
              // tintColor: '#ff0000'
            })
            .subscribe((result: any) => {
                if(result.event === 'opened') console.log('Opened');
                else if(result.event === 'loaded') console.log('Loaded');
                else if(result.event === 'closed') console.log('Closed');
              },
              (error: any) => console.error(error)
            );

          } else {
            // use fallback browser, example InAppBrowser
          }
        }
      );
  }

  envSettings() {
    this.router.navigateByUrl('/appinfo');
  }

  ionViewWillEnter() {
    this.menuCtrl.enable(false);
  }

  login() {
    this.authService.login(this.username.value, this.password.value)
      .then(
        data => {
          // login successful so redirect to return url
          this.loginFormGroup.reset();
          this.router.navigateByUrl(this.returnUrl);
        }
      );
    // },
    // error => {
    // login failed so display error
    //     this.alertService.error(error);
    //     this.loading = false;
    // });
  }
}
